import React from "react"
import './App.css';

var batTreeDevice;
var batTreeCharacteristic;

// State machine
// 1. Connect, and all that crap
// 2. Query for number of patterns
// 3. Gather all of the patterns, and print them
// 4. Query for number of animations
// 5. Gather all of the animations, and print them
// 6. Hang out ready to rock!
//    -- Open settings tab
//    7. Query brightness
//    8. Query volume
//    9. Set brightness
//    9a. Get brightness
//    10. Set volume
//    10a. Get volume
// 11. Set pattern or animation
// 12. Turn off the tree.

const BatMachine = {
  connect : {
    //nextState: queryNumPat
  },
  queryNumPat : {
    //nextState : gatherPatterns
  },
  gatherPatterns : {
    //nextState : queryNumAni
  },
  queryNumAni : {
    //nextState : queryAnimations
  },
  gatherAnimations : {

  },
  readyToRock : {

  }
}

let BatState = BatMachine.connect;
let numPatterns = 0;
let currentPattern = 0;
let numAnimations = 0;
let currentAnimation = 0;


class Header extends React.Component {
  constructor(props) {
    super(props);

    // Set title property
    this.title = props.title;
  }

  render() {
    return (
      <div>
        <h1>{this.title}</h1>
      </div>
    )
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);

    // Set this binding
    this.onClickConnect = this.onClickConnect.bind(this);
    this.handleChangeBlah = this.handleChangeBlah.bind(this);
    this.makePatButton = this.makePatButton.bind(this);
    this.makeAniButton = this.makeAniButton.bind(this);

    this.state = {
      connected: false,
      allPatterns: [],
      allAnimations: []
    }
  }

  onClickConnect() {
    // Try to find the BAT Tree device
    let filters = [];
    filters.push({name: "BAT Tree"});
    let options = {};
    options.filters = filters;
    console.log('Reqeusting Bluetooth Device...');
    console.log('with ' + JSON.stringify(options));

    // Request the device with the options
    navigator.bluetooth.requestDevice(options)

    // If success, connect to GATT server
    .then(device => {
      console.log('Connecting to GATT Server...');
      batTreeDevice = device;
      return batTreeDevice.gatt.connect();
    })

    // Grab the primary service for the BLE controller
    .then(server => {
      console.log('Getting Primary Service...');
      return server.getPrimaryService(0xFFE0);
    })

    // Connect to the characteristic
    .then(service => {
      console.log('Getting Custom Characteristic...');
      return service.getCharacteristic(0xFFE1);
    })

    // Register callback when connected to characteristic
    .then(characteristic => {
      console.log('We got all the way to the end!');
      batTreeCharacteristic = characteristic;
      batTreeCharacteristic.addEventListener('characteristicvaluechanged', this.handleChangeBlah);
      batTreeCharacteristic.startNotifications();

      //  Update the state so connect goes away
      this.setState(() => {
        return {
          connected: true
        }
      })
    })
    .then(() => {
      BatState = BatMachine.queryNumPat;
      batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('NumPat\n'));
    })
    .catch(error => {
      console.log('WRONG! ' + error);
    });

  }



  
  handleChangeBlah(event) {
    let respStr = String.fromCharCode.apply(null, new Uint8Array(event.target.value.buffer));
    if (respStr === "EOE") {
      console.log("Let us ignore the EOE, shall we?");
      return;
    }

    if (BatState === BatMachine.queryNumPat) {
      numPatterns = parseInt(respStr);
      console.log('The number of patterns is ' + numPatterns);

      // Now that we have the number of patterns, let's get them
      if (numPatterns === 0) {
        console.log('Uh... I guess I need to handle this. But for now, it will break me.');
        return;
      }

      // Send the query for the first pattern to start the fun
      batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('GetPat 0\n'));
      currentPattern = 0;
      BatState = BatMachine.gatherPatterns;
    }

    else if (BatState === BatMachine.gatherPatterns) {

      console.log('Pattern ' + currentPattern + ': ' + respStr);
      let localPatterns = this.state.allPatterns;

      localPatterns.push({
        name: respStr,
        index: currentPattern
      })
      currentPattern++;

      this.setState(() => {
        return (
          {
            allPaterns: localPatterns
          }
        )
      })

      if (numPatterns > currentPattern) {
        let queryString = 'GetPat ' + currentPattern + '\n';
        batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(queryString));
      }
      else {
        console.log('I have received all of my patterns?');
        console.log(localPatterns);
        BatState = BatMachine.queryNumAni;
        batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('NumAni\n'));
      }
    }

    else if (BatState === BatMachine.queryNumAni) {
      numAnimations = parseInt(respStr);

      if (numAnimations === 0) {
        console.log('Same blah blah broken blah blah');
        return;
      }

      // Send the query for the first animation to start the fun
      batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode('GetAni 0\n'));
      BatState = BatMachine.gatherAnimations;
    }

    else if (BatState === BatMachine.gatherAnimations) {
      console.log('Animation ' + currentAnimation + ': ' + respStr);
      let localAnimations = this.state.allAnimations;
      localAnimations.push({
        name: respStr,
        index: currentAnimation
      })

      currentAnimation++;

      this.setState(() => {
        return {
          allAnimations: localAnimations
        }
      })

      if (numAnimations > currentAnimation) {
        let queryString = 'GetAni ' + currentAnimation + '\n';
        batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(queryString));
      }
      else {
        console.log('I have received all of my animations?');
        console.log(localAnimations);

        BatState = BatMachine.readyToRock;
      }
    }

    else {
      console.log(`Response: ${respStr}`);
    }

  }

  makePatButton(data, idx) {
    return (
      <li key={data.name}>
        <button id={'SetPat ' + idx} onClick={(e) => {
          batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`${e.target.id}\n`));
          console.log(e.target.id)
        }}>
          {data.name}
        </button>
      </li>
    );
  }

  makeAniButton(data, idx) {
    return (
      <li key={data.name}>
        <button id={'SetAni ' + idx} onClick={(e) => {
          batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`${e.target.id}\n`));
          console.log(e.target.id)
        }}>
            {data.name}
        </button>
      </li>
    );
  }


  render() {
    return (
      <div className="App">
        <Header title='BAT Controller'/>
        
        {!this.state.connected &&
          <button onClick={this.onClickConnect}>
            Connect
          </button>
        }
        <ul>
          {this.state.allPatterns.map(this.makePatButton)}
        </ul>
        <ul>
          {this.state.allAnimations.map(this.makeAniButton)}
        </ul>
        {this.state.connected && 
        <div>
          <button onClick={() => {batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`IncBright\n`))}}>IncBright</button>
          <button onClick={() => {batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`DecBright\n`))}}>DecBright</button>
        </div>
        }
        {this.state.connected && 
        <div>
          <button onClick={() => {batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`IncVol\n`))}}>IncVol</button>
          <button onClick={() => {batTreeCharacteristic.writeValueWithoutResponse(new TextEncoder().encode(`DecVol\n`))}}>DecVol</button>
        </div>
        }
      </div>
    );
  }
}

export default App;
